'use client';
import { client } from '@/app/client';
import { useEffect, useState } from 'react';
import { IPlaylist } from '../page';
import { useParams } from 'next/navigation';
import { TrackItem } from '@spotify/web-api-ts-sdk';

export default function PlaylistPage() {
	const params = useParams<{ id: string }>();

	const [playlist, setPlaylist] = useState<IPlaylist | null>(null);
	const [tracks, setTracks] = useState<TrackItem[]>([]);
	console.log('ðŸš€ ~ PlaylistPage ~ tracks:', tracks);

	useEffect(() => {
		const getPlaylist = async () => {
			const playlist = await client.playlists.getPlaylist(params.id);
			setPlaylist(playlist);
		};
		const getTracks = async () => {
			const tracks = await client.playlists.getPlaylistItems(params.id);
			setTracks(tracks.items.map(item => item.track));
		};
		getPlaylist();
		getTracks();
	}, [params.id]);

	return (
		<div>
			<h1>{playlist?.name}</h1>
			<ul>
				{tracks.map(track => (
					<li key={track.id}>{track.name}</li>
				))}
			</ul>
		</div>
	);
}
